    cursor: pointer;
    transition: background-color 0.3s ease;
    border-radius: 90px;
}

.contactForm input[type="submit"]:hover {
    background-color: #4CAF50;
}

@media (max-width: 768px) {
    .contactForm {
        width: 90%;
    }
}

.modal {
    display: flex !important; /* Add this line */
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1000;
    -webkit-animation: fadeIn 1s;
            animation: fadeIn 1s;
}

.modalBtn {
    background-color: rgb(234, 169, 18);
    color: #fff;
    cursor: pointer;
    transition: background-color 0.3s ease;
    border-radius: 90px;
    padding: 10px;
    font-size: 15px;
}
  
.modalContent {
